import React, { useCallback } from "react";
import { withRouter } from "react-router";
import {authentication} from "../firebase";
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';

const Registrarse = ({ history }) => {
  const handleSignUp = useCallback(async event => {
    event.preventDefault();
    const { email, password } = event.target.elements;
    try {
      await authentication
        .createUserWithEmailAndPassword(email.value, password.value);
      history.push("/");
    } catch (error) {
      alert(error);
    }
  }, [history]);

  const useStyles = makeStyles((theme) => ({
   
    paper: {
      marginTop: theme.spacing(8),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    
    avatar: {
      margin: theme.spacing(1),
      backgroundColor: theme.palette.secondary.main,
    },
    form: {
      width: '100%', // Fix IE 11 issue.
      marginTop: theme.spacing(3),
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
    },
  }));
  
  const classes = useStyles();

  return (
    <Container component="main" maxWidth="xs">
    <CssBaseline />
    <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        
        <Typography component="h1" variant="h5">
          Registrarse
        </Typography><br/>
    <div> 
      <form onSubmit={handleSignUp}>
      <Grid container spacing={2}> <Grid item xs={12} sm={6}>
     <TextField autoComplete="fname" name="firstName" variant="outlined" required fullWidth
      id="firstName" label="Nombre"autoFocus/>
   </Grid> <Grid item xs={12} sm={6}>
   <TextField variant="outlined" required fullWidth id="lastName" label="Apellido" name="lastName" autoComplete="lname"/>
      </Grid> <Grid item xs={12}>
  <TextField  variant="outlined" required fullWidth id="email" label="Email"
name="email" autoComplete="email"/>
     </Grid> <Grid item xs={12}>
 <TextField variant="outlined" required fullWidth
 name="password" label="Contraseña"
  type="password" id="password" autoComplete="current-password"/>
 </Grid><Grid item xs={12}>
 <FormControlLabel control={<Checkbox value="allowExtraEmails" color="primary" />}
 label="I want to receive inspiration, marketing promotions and updates via email." />
            </Grid> </Grid>
          <Button className='btn'
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            
          > Registrarse </Button>
          <Grid container justify="flex-end">
            <Grid item>
              <Link href="/emprender/login" variant="body2">
                Already have an account? Sign in
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={5}>
      </Box>
     </div>
    </Container>
  );
}



export default withRouter(Registrarse);